<template>
  <section>
    <!-- 顶部菜单栏 -->
    <div class="menu-box">
      <div class="menu-con">
        <div
          class="menu-li"
          :class="{'active':menuInx==inx}"
          v-for="(ml,inx) in menuList"
          v-bind:key="inx"
          @click="changeMenu(inx)"
        >{{ml.name}}</div>
      </div>
    </div>
    <div class="menu-shadow"></div>
    <!-- 产品列表 -->

    <div class="p-ul">
      <a :href="cl.url" class="p-li" v-for="(cl,inx) in clist" v-bind:key="inx">
        <!-- 标题 -->
        <div class="title">
          <div class="txt1">{{cl.title}}</div>
          <div class="txt2">
            <img v-if="cl.logo=='bjrs'" src="../assets/img/bjrs.png" alt />
            <img v-if="cl.logo=='bnrs'" src="../assets/img/bnrs.png" alt />
            <img v-if="cl.logo=='fxlh'" src="../assets/img/fxlh.png" alt />
            <img v-if="cl.logo=='hkrs'" src="../assets/img/hkrs.png" alt />
            <img v-if="cl.logo=='rbcx'" src="../assets/img/rbcx.png" alt />
            <img v-if="cl.logo=='tabx'" src="../assets/img/tabx.png" alt />
            <img v-if="cl.logo=='ygbx'" src="../assets/img/ygbx.png" alt />
          </div>
        </div>
        <!-- 特色 -->
        <div class="tips">
          <div v-for="(ct,inx2) in cl.tips" v-bind:key="inx2" class="txt1">{{ct}}</div>
        </div>
        <!-- 基本介绍 -->
        <div class="con">
          <!-- 内容简介 -->
          <div class="box0">
            <div v-for="(d,inx2) in cl.detail" v-bind:key="inx2">{{d}}</div>
          </div>
          <!-- 价格部分 -->
          <div class="box1">
            <div class="txt1">{{cl.money}}</div>
            <div class="txt2">元起</div>
          </div>
        </div>
      </a>
    </div>
  </section>
</template>
<script>
let arr;
export default {
  data() {
    return {
      menuInx: 0,
      menuList: [
        { name: "全部" },
        { name: "新冠肺炎", type: 1 },
        { name: "重疾险", type: 2 },
        { name: "意外险", type: 3 },
        { name: "医疗险", type: 4 },
        { name: "财产责任", type: 5 }
      ],
      clist: []
    };
  },
  mounted() {
    arr = this.$store.state.product;
    this.changeData();
  },
  methods: {
    changeData(type) {
      let arr2 = [];
      if (type) {
        arr.forEach(ele => {
          if (ele.type == type) {
            arr2.push(ele);
          }
        });
      } else {
        arr2 = arr;
      }
      this.clist = arr2;
    },
    changeMenu(inx) {
      this.menuInx = inx;
      this.changeData(inx);
    }
  }
};
</script>
<style lang="scss" scoped>
.menu-box {
  width: 100%;
  overflow: auto;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  background-color: #fff;
  border-bottom: 1px solid #f5f5f5;
}
.menu-con {
  display: flex;
  padding: 0 6px;
  .menu-li {
    color: #666;
    padding: 28px 27px 24px;
    white-space: nowrap;
  }
  .active {
    color: #f58a43;
    font-weight: bold;
  }
}
.menu-shadow {
  height: 80px;
}
.p-ul {
  background: #f5f5f5;
  padding: 20px 30px;
  min-height: calc(100vh - 170px);
}
</style>